.section-title {
  @include container;

  margin: 0;
  margin-bottom: 48px;
  font-weight: 700;
  line-height: 32px;
  font-size: 32px;

  @include onTablet {
    margin-bottom: 56px;
    font-size: 40px;
    line-height: 40px;
  }

  @include onDesktop {
    font-size: 48px;
    line-height: 48px;
  }
}
